import React, { PureComponent, Suspense } from "react";
import Media from "./Media";
import { themeImpressionTracking } from "../../../../utils";
import { isTagShop, suiteTrack } from "../../../../actions/api";
import ViewCount from "../../Elements/ViewCount";
import Products from "./Products";
import AuthorDetails from "./AuthorDetails";


//const Products = React.lazy(() => import("./Products"));
//const AuthorDetails = React.lazy(() => import("./AuthorDetails"));
export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.mediaHeight = React.createRef();
  }
  componentDidMount() {
    const { Post, itemIndex, onClickPopUpSlider } = this.props
    if (suiteTrack || isTagShop) themeImpressionTracking(this.mediaHeight)
    if (Post.isPopUp) {
      onClickPopUpSlider(itemIndex, Post)
    }
  }

  render() {
    const { itemIndex, isVisible, clickToShowPopUp, ThemeStyle, CardStyle, Post, ProductSetting, sliderHeight, index } = this.props;
    const { style } = CardStyle
    const cardStyle = { backgroundColor: style.background, minHeight: sliderHeight };
    const isVideo = [3,5].includes(Post.type) ? true : false

    return <div data-id={Post.id} id={`tb_rt_id-${Post.id}`} className="tb_rtp_post_wrapper" ref={this.mediaHeight} style={{ padding: style.padding / 2 }} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} area-label={Post.content.text} role="article">
      <div className={`tb_rtp_post_in tb_icon_animate ${!isTagShop ? 'tb_rtp_post_in_animate' : ''}`} style={cardStyle} onClick={clickToShowPopUp(itemIndex, Post)} role="button" aria-label="Popup" tabIndex={0}>
        <div className="tb_rtp_post_media_wrapp">
          {isVideo && CardStyle.viewCountStatus == 1 && Post?.count.views > 0 ? <ViewCount count={Post?.count.views} /> : null}
          <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} isVisible={isVisible} index={index} />
          {!isTagShop ?
            <div className="tb_rtp_play_ico">
              <div className="tb__icon tb-play"><div></div></div>
            </div>
            : null}
        </div>
        
        {Post.isProduct ?
          <Products Post={Post} ProductSetting={ProductSetting} key={`product_slide${Post.id}`} />
          :
          <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} />
        }
       
      </div>
    </div>
  }
}
