import { isInstaUser } from "../../../../utils";
import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
const AuthorDetails = ({ Post, CardStyle, ProductSetting }) => {
  const { author, networkId } = Post
  const { ProductTitle } = ProductSetting
  const authorStatus = (CardStyle.author.status && !isInstaUser(author.name)) ? true : false;
  return (
    <div className="tb_rtp_wrap">
      <div className="tb_rtp_wrap_in" style={{ marginTop: !authorStatus ? 0 : '' }}>
        {authorStatus ? <div className='tb_rtp_media'><Author networkId={networkId} author={author} authorClass={'tb_rtp_a_img'} key={author.username} /> </div> : ''}
        {authorStatus ? <div className='tb_rtp_title_w'>
        <div className={`tb_rtp_authorname tb-cTBfont-${ProductTitle.varient}`}>{author.username ? author.username.length > 0 ? `${author.username}` : `` : ''}</div>
        </div> : ''}
        {CardStyle.timeStatus ? 
            <Time createdAt={Post.createdAt} timeClass={`tb_rtp_time tb-cTBfont-${CardStyle.author.variant}`} />
        : ''}
      </div>
    </div>
  );
}
export default AuthorDetails