import React from "react";
import Image from '../../Elements/Media/Image';
import Video from '../../Elements/Media/Video';
import { isTagShop } from "../../../../actions/api";
const Media = ({ Post, ThemeStyle, isVisible, index }) => {
  const isVideo = [3,5].includes(Post.type) && isVisible && (ThemeStyle.autoPlay == 1 || !isTagShop) ? true : false
  return <div className="tb_rtp_media_wrap">
    {isVideo ?
      <Video VideoClass="tb_rtp_video" Post={Post} size={178} isCover={true} muted={true} autoPlay={true} index={index} />
      :
      <Image ImageClass="tb_rtp_image" Post={Post} size={178} index={index} />
    }
  </div>
}
export default Media